import { Layout, Input ,Menu, Button, Empty,PageHeader ,Select } from 'antd';
import React from 'react';
import './Star.css'
import { PlusOutlined,MenuOutlined } from '@ant-design/icons';
const {  Content,  Sider } = Layout;
const { Option } = Select;
const { Search } = Input;


export default function Star() {

    const  menuList1 = [
        {
          key: '1',
          title: '',
        },
        ]

    const selectBefore = (
        <Select defaultValue="" className='quanbuleixing'>
            <Option value="quanbu"></Option>
            <Option value="wendang"></Option>
            <Option value="liucheng"></Option>
            <Option value="xiangmu"></Option>
            <Option value="kehu"></Option>
            <Option value="xiaoxi"></Option>
            <Option value="qita"></Option>
        </Select>
        );

    const renderMenu =(menuList1)=>{
        return menuList1.map(item=>{
            return <Menu.Item key={item.key} >{item.title}</Menu.Item>
        })
        }

    const onClick = (key) => {
        console.log(key);
        };

    const onSearch = () => {

    }

  return (
    <div>
        <Layout>
            <Sider
            style={{overflowY:'hidden',background:'#fff',padding:'12px 0px', borderRight: '1px solid #e2e2e2',height:'454px'}}
            breakpoint="lg"
            collapsedWidth="0"
            onBreakpoint={(broken) => {
                console.log(broken);
            }}
            onCollapse={(collapsed, type) => {
                console.log(collapsed, type);
            }}
            >
                <div style={{
                        padding: '4px 11px',
                        margin:'0px 0px 10px'
                    }}>
                    <MenuOutlined />
                    
                    <Button icon={<PlusOutlined />} style={{float:'right',fontSize:'30px',marginTop:'-5px'}}/>
                </div>
                <div style={{borderTop: '1px solid #e2e2e2',flex:1,"overflow":"auto"}}>
                    <Menu
                        defaultSelectedKeys="1"
                        theme="light"
                        style={{ height: '100%'}}
                    >   
                    {renderMenu(menuList1)}
                    </Menu>
                </div>
            </Sider>
            <Layout>
            <Content>
                <Layout>
                    <PageHeader style={{height:'61px',background:'#fff'}}>
                        <Button icon={<PlusOutlined />} onClick={onClick}>
                            
                        </Button>
                        <Search
                            addonBefore={selectBefore}
                            placeholder=""
                            allowClear
                            onSearch={onSearch}
                            style={{
                                width: 304,
                                paddingLeft: '50px'
                            }}
                        />
                        <Button style={{height:'32px',float:'right', right:'10px'}}>
                            
                        </Button>
                        <Button style={{height:'32px',float:'right', right:'20px'}}>
                            
                        </Button>
                    </PageHeader>
                    <Empty image={Empty.PRESENTED_IMAGE_SIMPLE} style={{marginTop:'150px'}}/>
                </Layout>
            </Content>
            </Layout>
        </Layout>
    </div>
  )
}
